.main__candidats, .candidats {
    min-height: 100vh;
    background-color: white;
    height: 100%;
    width: 100%;
    &__whiteH {
        margin: 0 auto;
        display: block;
        width: 300px;
        padding-top: 38px;
        margin-bottom: 10px;
        @media (min-width: 768px) {
            padding-top: 5px; }
        @media (min-width: 812px) {
            width: 180px; }
        @media (min-width: 1024px) {
            width: 500px; }
        @media (min-width: 1366px) {
            width: 800px; } }
    &__title {
        width: 90%;
        margin: 0 auto 10px auto;
        text-align: center;
        font-size: 18px;
        @media (min-width: 812px) {
            margin-bottom: 0px; }
        @media (min-width: 1366px) {
            margin-bottom: 20px;
            font-size: 22px; } } }
